


body {
  font-family: 'Nunito', sans-serif;
  background:url(/images/background2.png); 
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position: center;
  background-size: contain;
  background-color: #b5f1fc; 
}

a, a:hover, a:focus, a:active {
  text-decoration: none;
  color: inherit;
}


*:focus {
  outline: none;
}

.body {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 5px 20px;
  align-content: center;
  align-items: center;
  align-self: center;
  font-size: 14px;
  line-height: 20px;
}


h1 {
  text-align: center;
  font-size: 22px;
  margin-top: 20px;
  margin-bottom: 0px;
}

h4 {
  text-align: center;
  font-size: 15px;
}



footer {
  height: 40px;
  width: 100%;
  color: #51B8F9;
  margin: 25px 0px 20px;
  display: flex;
  /* border-top: 1px solid grey; */
  align-items: center;
  justify-content: center;
  margin:0
}

footer > h4 {
  font-size: 25px;
}

/* .Navbar {
  position: fixed;
  top: 0px;
} */

input {
  margin: 0px;
  padding: 0px;
  font-size: 13px;
  font-style: italic;
}

p {
  margin: 3px 0px;
}


.myButton {
  background-color: #51B8F9;
  font-size: 12px;
  font-weight: bold;
  color: white;
  border-radius: 5px;
  border-color: #51B8F9;
  margin: 10px 3px;
  padding: 7px;
  width: 90px;
  min-height: 30px;
}


.myButton:hover {
  /* background-color: #1298eb;*/
  background-color: #05d3c9;
  border-color: #05d3c9;
  box-shadow: 2px 2px 5px 0px rgba(0, 0, 0, 0.26);
}


.disabled {
  background-color: rgb(165, 165, 165);
  border-color: #5c5e69;
}

.disabled:hover {
  background-color: rgb(165, 165, 165);
  border-color: #5c5e69;
  box-shadow: none;
}


.main-content {
  margin-top: 20px;
  width: 100%;
  max-width: 600px;
  min-width: 300px;
}


.sign-log-form {
  display: flex;
  flex-direction: column;
  text-align: center;
  margin: 20px 0px;
  max-width: 300px;
  min-width: 300px;
}

.login-label {
  margin: 8px;
  font-weight: bold;
}

.label-user {
  font-size: 14px;
  font-weight: bold;
}

.home-doctor-card {
  width: 100%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  box-shadow: 4px 4px 10px 0px rgba(0, 0, 0, 0.26);
  margin: 10px 0px;
  border: 2px solid #51B8F9;
  border-radius: 10px;
  background-color: white;
}

.home-doctor-card > * {
  padding: 15px;
}


.search-doc-details-text {
  max-width: 180px;
}

.index-info-card {
  display: flex;
  justify-content: space-evenly ;
  width: 100%;
  max-width: 600px;
  justify-self: center;
  padding: 20px 10px;
  box-shadow: 5px 5px 15px 0px rgba(0, 0, 0, 0.336);
  margin: 10px 0px;
  /* border: 2px solid #51B8F9; */
  border: 2px solid transparent;
  border-radius: 100px;
  background-color: #ffffff;
}

.index-info-card img {
  width: 170px;
}


.index-img-card {
  max-width: 200px;
  width: 30%;
  font-size: 15px;
  font-weight: bold;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;

}

.hidden-button{
  display: none
}


.appointment-card {
  padding: 20px 35px;
  border-radius: 10px;
  border: 2px solid #51B8F9;
  background-color: white;
  box-shadow: 4px 4px 5px 0px rgba(0, 0, 0, 0.212);
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.doc-img {
  width: 120px;
  height: 120px;  
  object-fit: cover;
  border-radius: 50%;
}

.edit-profile {
  background-color: transparent;
  border-color: transparent;
}


.img-edit-btn-profile {
  width: 18px;
  margin: 0px;
}




.home-search-bar {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin: 40px 0px 0px 0px;
}


.input-search {
  height: 35px;
  width: 220px;
  text-align: center;
  border-radius: 10px;
  border-color: #51B8F9;
  box-shadow: 4px 4px 5px 0px rgba(0, 0, 0, 0.212);

}



/* DOCTOR / PATIENT PROFILE */

.profile-field {
  display: flex;
  justify-content: space-between;
  margin: 10px 0px;
}

.readonly-field{
  background-color:transparent;
  border: none;
  /* padding: 4px; */
  outline:none;
  width: 60%;

}


.profile-label {
  width: 20%;
}

.profile-field-img {
  justify-content: center;
  margin-bottom: 40px;
  margin-top: 20px;
}

.profile-field-img > img {
  width: 130px;
  height: 130px;
  object-fit: cover;
}


.patient-cal-date-field {
  display: flex;
  align-items: center; 
  margin: 20px 0px 15px; 
}

hr {
  width: 100%;
  height: 1px;
  background-color: #51B8F9;
  border-color: transparent;
}

.time-planner {
  margin-top: 0px;
}

.medicine-line {
  text-align: center;
}

.patient-cal-day-times {
  padding: 5px;
  margin: 5px;
  border-radius: 10px;
  min-height: 300px;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  border: 2px solid #51B8F9;
  background-color: white;
  box-shadow: 4px 4px 5px 0px rgba(0, 0, 0, 0.212);

}

.user-type-choice {
  font-size: 10px;
}

.prescription-form{
  border: 2px solid #51B8F9;
  box-shadow: 4px 4px 5px 0px rgba(0, 0, 0, 0.212);
  background-color: white;
  width: 100%;
  padding: 0px 10px; 
  border-radius: 10px;
}

.presription-inputs {
  display: flex;
  flex-direction: column;
  margin: 5px 0px;
}

.presription-inputs > * {
  margin: 2px 0px;
}

.save-pres-btn {
  width: 60%;
}

.event-content{
  display:flex;
  justify-content: space-around;
}


.profile-page-content {
  font-size: 15px;
  margin-top: 20px;
  background-color: white;
  border: 2px solid #51B8F9;
  box-shadow: 4px 4px 5px 0px rgba(0, 0, 0, 0.212);
  padding: 0 30px;
  border-radius: 20px;
}


.calendar-day {
  margin: 0 10px;
  padding: 5px;
  width: 40px;
  height: 20px;
  font-size: 18px;
  display: flex;
  text-align: center;
  align-content: center;
  align-items: center;
}


.appointment-details-card {
  border: 2px solid #51B8F9;
  background-color: white;
  box-shadow: 4px 4px 5px 0px rgba(0, 0, 0, 0.212);
  border-radius: 20px;
}

.top-card-app-details {
  margin-top: 10px;
  text-align: center;
}

.bottom-card-app-details {
  display: flex;
  align-items: center;
  justify-content: space-around;
  margin-bottom: 10px;
}

.btn-app-details {
  display: flex;
  flex-direction: column;
}

.report-upload {
  /* border: 2px solid red; */
  display: flex;
  flex-direction: column;
  align-items: center;
  align-self: center;
  padding: 5px;
}


#report-group {
  width: auto;
}

.prescrition-form-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0 15px;
  border-radius: 20px;
}

.medications-added-card {
  padding: 0px;
  margin: 0px;
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.added-med {
  font-size: 12px;
  border: 2px solid #51B8F9;
  box-shadow: 4px 4px 5px 0px rgba(0, 0, 0, 0.212);
  border-radius: 20px;
  margin: 5px;
  padding: 5px;
  min-width: 200px;
  background-color: white;
}


.add-med-btn {
  background-color: transparent;
  border-color: transparent;
  width: 100%;
}


.booking-instr-card {
  margin-top: 40px;
  border: 2px solid red;
  border-radius: 10px;
  /* padding: 5px; */
}

.booking-instructions-text {
  padding-left: 25px;
  padding-right: 10px;
  text-align: justify;
}


/* NAVBAR */

.navbar-all {
  display: flex;
  flex-direction: column;
}


.navbar-logo {
  display: flex;
  justify-content: center;
}


.header-logo {
  width: 350px;
}


 .navbar-menu {
  margin: 10px 0px;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  width: 100%;
} 


.navbar {
  margin: 10px 0px;
  display: flex;
  align-items: flex-end;
  justify-content: center;
  width: 100%;
} 


.nav-link {
  margin: 1px;
  text-align: center;
}


.navbar-light .navbar-nav .nav-link {
  color: white;
  background-color: #02BDFF;

}

.nav-tabs .nav-item.show .nav-link, .nav-tabs .nav-link.active {
  background-color: white;
  font-size: 16px;
  box-shadow: 4px -2px 10px 0px rgba(0, 0, 0, 0.212);
  font-weight: bold;
}



.log-sign-btn {
  background-color: #51B8F9;
  color: white;
  font-size: 15px;
  width: 95px;
  text-align: center;
}


.log-sign-btn:hover {
  color: white;
  background-color: #05d3c9;
  box-shadow: 2px 2px 5px 0px rgba(0, 0, 0, 0.26);
}

/* FULL CALENDAR */

.fc {
  width: 600px;
  height: 400px;
  margin-bottom: 30px;
  margin-top: 30px;
  padding: 8px;
  border: 1px solid rgb(192, 192, 192);
  border-radius: 10px;
  background-color: white;
}

.calendar-doc {
  width: 800px;

}

.fc a {
  text-decoration: none;
  color: inherit;
}



.fc .fc-timegrid-slot {
  border: 1px solid rgb(192, 192, 192);
}

.calendar-card {
  display: flex;
  justify-content: center;
  align-items: center;
  align-content: center;

}

.fc-toolbar-title{
  font-size: 1em!important;
}

.fc-timegrid-slot{
  background-color: white!important
}


.fc-timegrid-bg-harness {
  /* background: red; */
  background-color: #bfe7ff;
}

/* .fc-event-main {
  background-color: #51B8F9;
} */

.fc-button-group {
  width: 100px;
  margin-right: 5px;
}

/* .fc-non-business {
  background-color: red;
} */




/* MEDIA QUERY FOR RESPONSIVENESS */

@media only screen and (max-width: 500px) {

  .fc {
    width: 350px;
    height: 400px;
  }

  .index-info-card img {
    width: 130px;
  }



}




